body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  background-color: hsl(36, 100%, 96%);
}

.App {
  display: flex;
  height: 100vh;
  flex-direction: column;
  /* align-items: center; */
}

.spacer {
  display: flex;
  flex-grow: 1;
}

:root {
  --height: 5vh;
  --link-color: #fff;
  --text-color: #fff;
  --orange-color: hsl(32, 100%, 53%);
}

.navigation {
  /* min-height: calc(var(--height) + 70px); */
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  background-color: rgb(161, 161, 161);
  /* overflow: hidden; */
}
.navLinks {
  display: flex;
  list-style-type: none;
  /* height: var(--height); */
}
.link {
  display: inline-block;
  padding: 0 0.5rem;
  text-decoration: none;
  text-align: center;
  line-height: calc(5vh);
  line-height: calc(var(--height));
  color: white;
  font-size: 0.8rem;
}
.link:hover {
  background-color: hsl(32, 100%, 53%);
  background-color: var(--orange-color);
  color: hsl(216, 100%, 29%);
  font-weight: bold;
}
.link.current {
  background-color: hsl(32, 100%, 53%);
  background-color: var(--orange-color);
}
.logo > img {
  max-height: calc(5vh + 40px);
  max-height: calc(var(--height) + 40px);
}
.contact {
  color: hsl(216, 100%, 29%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/******************** media query *********************/

@media (max-width: 1037px) {
  .navLinks {
    order: 3;
    align-items: flex-end;
  }
  .link {
    line-height: normal;
    padding: 0.2rem 0.8rem;
  }
}

.mainFooter {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  width: 100%;
}

.col1 {
  flex: 1 1 100%;
  padding: 0.7rem 0.6rem;
}
.col2 {
  flex: 1 1 300px;
  padding: 0.7rem 0.6rem;
}
.partenaires {
  background-color: hsl(36, 100%, 85%);
}
.certif {
  height: 120px;
  background-color: hsl(36, 100%, 75%);
}
.follow {
  height: 120px;
  /* background: linear-gradient(
      hsla(36, 100%, 60%, 0.9),
      hsla(36, 100%, 60%, 0.8)
    ),
    url("../img/graduation.jpg"); */
  /* background-size: cover; */
  background-color: hsl(36, 100%, 60%);
}
.map {
  background-color: hsl(36, 100%, 50%);
}

.partLogo,
.certifLogo,
.followLogo {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
}

h3 {
  margin-bottom: 1.5rem;
  color: #333;
}

.cards {
  height: 450px;
  width: 350px;
  border: 2px solid #eee;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  margin: 0.2rem 1.2rem;
}

.card_title {
  width: 100%;
  min-height: 75px;
  background: linear-gradient(
      hsla(216, 100%, 29%, 1),
      hsla(216, 100%, 50%, 0.5)
    ),
    url(/static/media/graduation.b83d6b88.jpg) no-repeat center;
  background-size: cover;
  text-align: center;
  padding: 1.2rem 0;
}
.card_title h3 {
  padding: 0 1.2rem;
  margin: 0;
  color: #eee;
  font-size: 1.2rem;
}
.card_body {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 1rem;
  background: linear-gradient(hsla(36, 100%, 50%, 0.8), #eee);
  /* background: linear-gradient(hsla(36, 100%, 50%, 0.8), #eee),
    url("../img/graduation.jpg") no-repeat center;
  background-size: cover; */
}
.card_body p {
  font-size: 1.2rem;
  color: #222;
  text-align: center;
}
.card_pied {
  min-height: calc(15% + 15px);
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgb(161, 161, 161);
}
.card_btn {
  background: transparent;
  border: 2px solid hsla(36, 100%, 50%, 1);
  border-radius: 5px;
  padding: 0.8rem 1.2rem;
  color: #eee;
  font-size: 1rem;
  font-weight: bold;
  outline: none;
}
.card_btn:hover {
  cursor: pointer;
  box-shadow: 0 10px 5px -3px rgba(0, 0, 0, 0.5);
  transform: scale(1.1);
  transition: transform ease-in 100ms;
  color: hsl(216, 100%, 29%);
}

.pageAccueil {
  width: 100%;
}
.hero_image {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  min-height: calc(30vh + 100px);
  background: linear-gradient(hsl(216, 100%, 29%), hsla(36, 100%, 50%, 0.452)),
    url(/static/media/4.fe490d32.jpg) no-repeat center top;
  /* background-attachment: fixed; */
  background-size: cover;
}
.hero_txt {
  max-width: 50%;
  color: #eee;
}
.hero_txt h1 {
  font-size: 2.5rem;
  margin: 1.2rem 0;
}
.hero_txt p {
  font-size: 1.3rem;
  margin: 1.2rem 0;
}
.big_btn {
  background: transparent;
  border: 2px solid hsl(36, 100%, 50%);
  border-radius: 5px;
  padding: 0.8rem 1.2rem;
  color: #eee;
  font-size: 1.5rem;
  font-weight: bold;
  outline: none;
}

.big_btn:hover {
  cursor: pointer;
  box-shadow: 0 15px 10px -3px rgba(0, 0, 0, 0.5);
  transform: scale(1.1);
  transition: transform ease-in 100ms;
  color: hsl(216, 100%, 29%);
}

.formationUne,
.actualiteUne {
  text-align: center;
  margin: 1rem 0;
  padding-bottom: 1rem;
  background: hsl(140, 80%, 29%);
}
.formationUne h1,
.actualiteUne h1 {
  text-align: center;
  padding: 1.2rem 0;
  color: #eee;
  margin-bottom: 1rem;
}
.actualiteUne {
  background: #b39b77;
}

.listCards {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.mt2 {
  margin: 1.2rem 0;
}

.formations {
  text-align: center;
  margin: 1rem 0;
  padding-bottom: 1rem;
  /* background: hsl(140, 80%, 29%); */
}
.formations h1 {
  margin-bottom: 1.5rem;
}

.formationDetails {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  margin: 1rem auto;
}
.title {
  color: blue;
  text-align: center;
  width: 100%;
  padding: 1rem;
  border: 2px solid hsl(32, 100%, 53%);
  border-radius: 10px 10px 0 0;
}

.content {
  flex: 5 1 auto;
  padding: 0.5rem;
}
.contentgroupe li {
  margin-left: 1.5rem;
}
.contentgroupe h1 {
  font-size: 1.2rem;
  text-align: right;
  border-bottom: 1px solid hsl(32, 100%, 53%);
  margin-bottom: 0.5rem;
}
.sidebar {
  flex: 1 1 200px;
  background: hsl(140, 80%, 29%);
  padding: 0.5rem;
}
.sidegroupe {
  margin-bottom: 1.2rem;
}
.sidegroupe h3 {
  font-size: 0.9rem;
  margin: 0;
  padding: 0.2rem;
  color: white;
}
.sidebar p {
  padding: 0 0.5rem;
  margin: 0;
}
.partprog {
  margin-bottom: 0.5rem;
}

